"use client";
import React, { useState } from "react";

export default function Dropdown({
  items = [],
  placeholder = "Select Value",
  onSelect,
  className = "",
  showInitial = false,
  showIcons = false,
  styling = "",
}) {
  const [open, setOpen] = useState(false);
  const [selectedValue, setSelectedValue] = useState(null);

  const handleDropdown = (item) => {
    setSelectedValue(item);
    setOpen(false);
    onSelect?.(item);
  };

  return (
    <div className="relative inline-block text-left">
      {/* Dropdown button */}
      <button
        onClick={() => setOpen(!open)}
        className={`px-3 py-1.5 rounded-md text-sm font-medium cursor-pointer outline-none transition-all duration-150 
          ${styling} ${className}`}
      >
        {selectedValue
          ? showInitial
            ? selectedValue.value.charAt(0).toUpperCase()
            : showIcons
            ? selectedValue.icons
            : selectedValue.label
          : placeholder}
      </button>

      {/* Dropdown list */}
      {open && (
        <ul className="absolute left-0 w-[180px] bg-zinc-900 border border-zinc-800 mt-1 rounded-lg shadow-md z-10">
          {items.map((item, idx) => (
            <li
              key={idx}
              onClick={() => handleDropdown(item)}
              className="p-2 text-sm text-gray-200 hover:bg-zinc-800 cursor-pointer rounded-md transition-colors duration-150"
            >
              {item.label}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
